
body{
  background: #fefefe;
}
.everything{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.wrapper{
  display: grid;
  grid-gap: 3px;
  padding-top: 20px;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: 100px 100px 100px;
  grid-template-areas: 
  "h h h h h h h h h h"
  "f f f f f f f f f f"
  "o o o o o o o o o o"
  ;
}
.header{
  grid-area: h;
}
.form{
  grid-area: f;
}
.output{
  grid-area: o;
}

.contain{
    display: grid;
    grid-gap: 15px;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: 40px 40px;
    grid-template-areas:
      "t t t l l l"
      ". . s s . ."
    ;
}
.translate{
    grid-area: t;
}
.lang{
    grid-area: l;
}
.submitBtn{
    grid-area: s;
}

.head{
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 24px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    color: #085972b9;
}

h1[data-v-d14401cc], h2[data-v-d14401cc] {
  font-weight: normal;
}
ul[data-v-d14401cc] {
  list-style-type: none;
  padding: 0;
}
